  -moz-border-radius: 100px 100px 100px 0px;
  -webkit-border-radius: 100px 100px 100px 0px;
  border: 0px solid #000000;
  box-shadow: 4px -2px 6px 0px rgba(0, 0, 0, 0.3);
}

.button-resources {
