  -moz-border-radius: 100px 100px 0px 100px;
  -webkit-border-radius: 100px 100px 0px 100px;
  border: 0px solid #000000;
  box-shadow: -4px -3px 6px 0px rgba(0, 0, 0, 0.3);
}

.button-statistics,.button-game {
  border-radius: 100px 0px 100px 100px;
  -moz-border-radius: 100px 0px 100px 100px;
  -webkit-border-radius: 100px 0px 100px 100px;
  border: 0px solid #000000;
  box-shadow: -4px 3px 6px 0px rgba(0, 0, 0, 0.3);
}

.button-survey {
  border-radius: 0px 100px 100px 100px;
  -moz-border-radius: 0px 100px 100px 100px;
  -webkit-border-radius: 0px 100px 100px 100px;
  border: 0px solid #000000;
  box-shadow: 4px 3px 6px 0px rgba(0, 0, 0, 0.3);
}

.button-home:hover {
  background-color: #b6fc9f;
  -webkit-transform: scale(1.3);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
}

#button-menu {
  cursor: pointer;
  padding: 10px 35px 16px 0px;
  height: 25%;
  margin-left: 20px;
  margin-right: 20px;
  z-index: 1;
}

#button-menu span,#button-menu span:before,#button-menu span:after {
  cursor: pointer;
  border-radius: 1px;
  height: 4px;
  width: 30px;
  background: #ECEDD0;
  position: absolute;
  display: block;
  content: '';
}

#button-menu span:before {
  top: -10px;
}

#button-menu span:after {
  bottom: -10px;
}

#button-menu span,#button-menu span:before,#button-menu span:after {
  transition: all 500ms ease-in-out;
}

#button-menu.active span {
  background-color: transparent;
}

#button-menu.active span:before,#button-menu.active span:after {
  top: 0;
}

#button-menu.active span:before {
  transform: rotate(45deg);
}

#button-menu.active span:after {
  transform: rotate(-45deg);
}

